import React, {useState} from 'react';
import {Button, Modal} from "react-bootstrap";

const left = <svg className="bi bi-arrow-left" width="1em" height="1em" viewBox="0 0 16 16" fill="currentColor"
                  xmlns="http://www.w3.org/2000/svg">
    <path fillRule="evenodd"
          d="M5.854 4.646a.5.5 0 0 1 0 .708L3.207 8l2.647 2.646a.5.5 0 0 1-.708.708l-3-3a.5.5 0 0 1 0-.708l3-3a.5.5 0 0 1 .708 0z"/>
    <path fillRule="evenodd" d="M2.5 8a.5.5 0 0 1 .5-.5h10.5a.5.5 0 0 1 0 1H3a.5.5 0 0 1-.5-.5z"/>
</svg>

const right = <svg className="bi bi-arrow-right" width="1em" height="1em" viewBox="0 0 16 16" fill="currentColor"
                   xmlns="http://www.w3.org/2000/svg">
    <path fill-rule="evenodd"
          d="M10.146 4.646a.5.5 0 0 1 .708 0l3 3a.5.5 0 0 1 0 .708l-3 3a.5.5 0 0 1-.708-.708L12.793 8l-2.647-2.646a.5.5 0 0 1 0-.708z"/>
    <path fill-rule="evenodd" d="M2 8a.5.5 0 0 1 .5-.5H13a.5.5 0 0 1 0 1H2.5A.5.5 0 0 1 2 8z"/>
</svg>

const down = <svg className="bi bi-arrow-down" width="1em" height="1em" viewBox="0 0 16 16" fill="currentColor"
                  xmlns="http://www.w3.org/2000/svg">
    <path fill-rule="evenodd"
          d="M4.646 9.646a.5.5 0 0 1 .708 0L8 12.293l2.646-2.647a.5.5 0 0 1 .708.708l-3 3a.5.5 0 0 1-.708 0l-3-3a.5.5 0 0 1 0-.708z"/>
    <path fill-rule="evenodd" d="M8 2.5a.5.5 0 0 1 .5.5v9a.5.5 0 0 1-1 0V3a.5.5 0 0 1 .5-.5z"/>
</svg>

const up = <svg className="bi bi-arrow-up" width="1em" height="1em" viewBox="0 0 16 16" fill="currentColor"
                xmlns="http://www.w3.org/2000/svg">
    <path fill-rule="evenodd" d="M8 3.5a.5.5 0 0 1 .5.5v9a.5.5 0 0 1-1 0V4a.5.5 0 0 1 .5-.5z"/>
    <path fill-rule="evenodd"
          d="M7.646 2.646a.5.5 0 0 1 .708 0l3 3a.5.5 0 0 1-.708.708L8 3.707 5.354 6.354a.5.5 0 1 1-.708-.708l3-3z"/>
</svg>

const trash = <svg className="bi bi-trash" width="1em" height="1em" viewBox="0 0 16 16" fill="currentColor"
                   xmlns="http://www.w3.org/2000/svg">
    <path
        d="M5.5 5.5A.5.5 0 0 1 6 6v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5zm2.5 0a.5.5 0 0 1 .5.5v6a.5.5 0 0 1-1 0V6a.5.5 0 0 1 .5-.5zm3 .5a.5.5 0 0 0-1 0v6a.5.5 0 0 0 1 0V6z"/>
    <path fill-rule="evenodd"
          d="M14.5 3a1 1 0 0 1-1 1H13v9a2 2 0 0 1-2 2H5a2 2 0 0 1-2-2V4h-.5a1 1 0 0 1-1-1V2a1 1 0 0 1 1-1H6a1 1 0 0 1 1-1h2a1 1 0 0 1 1 1h3.5a1 1 0 0 1 1 1v1zM4.118 4L4 4.059V13a1 1 0 0 0 1 1h6a1 1 0 0 0 1-1V4.059L11.882 4H4.118zM2.5 3V2h11v1h-11z"/>
</svg>


function TaskCard(props) {

    const [show, setShow] = useState(false)

    const handleShow = () => {
        setShow(true)
    }

    const handleClose = () => {
        setShow(false)
    }

    const priority = props.task.priority

    const handleRight = () => props.changeStatus({id: props.task.id, direction: 'right'})
    const handleLeft = () => props.changeStatus({id: props.task.id, direction: 'left'})

    const handleUp = () => props.changePriority({id: props.task.id, direction: 'up'})
    const handleDown = () => props.changePriority({id: props.task.id, direction: 'down'})

    // const deleteTask= (id) => {
    //     props.onTaskDelete(id)
    // }
    const handleDelete = () => {
        props.deleteTask(props.task)
        handleClose()
    }

    return (

            <div className="card bg-light mb-3">
                <div className="card-body">

                    {
                        (priority < 3) && <div onClick={handleUp}>{up}</div>
                    }


                    {
                        (priority > 1) && <div onClick={handleDown}>{down}</div>
                    }
                    {
                        priority === 1 &&
                            <span className="badge badge-success">
                                {props.priorities.find(el => el.id === props.task.priority).priority}
                            </span>
                    }
                    {
                        priority === 2 &&
                        <span className="badge badge-warning">
                                {props.priorities.find(el => el.id === props.task.priority).priority}
                            </span>
                    }
                    {
                        priority === 3 &&
                        <span className="badge badge-danger">
                                {props.priorities.find(el => el.id === props.task.priority).priority}
                            </span>
                    }

                    <h5 className="card-title">{props.task.name}</h5>

            <span onClick={handleLeft}>
                {(props.task.status !== 'todo') && left}
            </span>
            <span onClick={handleRight}>
                {(props.task.status !== 'done') && right}
            </span>
                    {/*<div className='text-right' onClick={() => deleteTask(props.task.id)}>*/}
                    {/*{trash}*/}
                    {/*</div>*/}


                        <span className='float-right' onClick={handleShow}>
                            {trash}
                        </span>

                        <Modal show={show} onHide={handleClose}>
                            <Modal.Header closeButton>
                                <Modal.Title>Delete task</Modal.Title>
                            </Modal.Header>
                            <Modal.Body>Are you sure ?</Modal.Body>
                            <Modal.Footer>
                                <Button variant="secondary" onClick={handleClose}>
                                    Close
                                </Button>
                                <Button variant="primary" onClick={handleDelete}>
                                    Delete
                                </Button>
                            </Modal.Footer>
                        </Modal>

                </div>
            </div>

    );
}

export default TaskCard;
