import React, {useState} from 'react';
import Column from "./Column";
import { v4 as uuidv4 } from 'uuid';
uuidv4(); // â‡¨ '9b1deb4d-3b7d-4bad-9bdd-2b0d7b3dcb6d'

const initialTasks = [
    {id: uuidv4(), name: 'Create F1', priority: 1, status: 'todo'},
    {id: uuidv4(), name: 'Create F2', priority: 2, status: 'review'},
    {id: uuidv4(), name: 'Create F3', priority: 2, status: 'todo'},
    {id: uuidv4(), name: 'Create F4', priority: 3, status: 'done'},
    {id: uuidv4(), name: 'Create F5', priority: 3, status: 'progress'},
    {id: uuidv4(), name: 'Create F6', priority: 2, status: 'done'},
    {id: uuidv4(), name: 'Create F5', priority: 3, status: 'progress'},
    {id: uuidv4(), name: 'Create F6', priority: 1, status: 'done'},
    {id: uuidv4(), name: 'Create F1', priority: 1, status: 'todo'},
    {id: uuidv4(), name: 'Create F2', priority: 1, status: 'review'},
    {id: uuidv4(), name: 'Create F3', priority: 2, status: 'todo'},
]

const priorities = [
    {id: 3, priority: 'High'},
    {id: 2, priority: 'Medium'},
    {id: 1, priority: 'Low'},
]

const statuses = ['todo', 'progress', 'review', 'done']

function App() {
    const [tasks, setTasks] = useState(initialTasks)
    const [isOpenCreateTaskForm, setIsOpenCreateTaskForm] = useState(false)
    const [taskInput, setTaskInput] = useState('')
    const [isActiveButtonTaskCreate, setIsActiveButtonTaskCreate] = useState(false)
    const [status, setStatus] = useState('')

    const openCreateTaskForm = () => {
        setIsOpenCreateTaskForm(true)
    }

    const onTaskChange = (e) => {
        setIsActiveButtonTaskCreate(e.target.value.length > 4)
        setTaskInput(e.target.value);
    }

    const taskSubmit = (e) => {
        // e.preventDefault();
        // console.log(taskInput);
        // taskReset();
        e.preventDefault();
        const newTask = {
            id: uuidv4(),
            name: taskInput,
            priority: 1,
            status: status
        };
        setTasks([...tasks, newTask])
        taskReset();
    };

    const taskReset = () => {
        setTaskInput('');
        setIsOpenCreateTaskForm(false);
        setIsActiveButtonTaskCreate(false);
    };

    const changeStatus = ({id, direction}) => {
       console.log(id, direction)

        const updatedTasks = tasks.map(el => {
            if(el.id === id){
               if(direction === 'left' ){
                   el.status = statuses[statuses.indexOf(el.status) - 1];
               }
                if(direction === 'right'){
                    el.status = statuses[statuses.indexOf(el.status) + 1];
                }
               return el;
            } else return el;
        });
       setTasks(updatedTasks);
    };

    // const onTaskDelete = (id) => {
    //     const updatedTasks = tasks.filter(el => el.id !== id);
    //     setTasks(updatedTasks);
    // }
    const deleteTask = (task) => {
        const updatedTasks = tasks.filter(obj => obj.id !== task.id)
        setTasks(updatedTasks)
    }

    const changePriority = ({id, direction}) => {
        const updatedTasks = tasks.map(task => {
            if(task.id === id) {
                if (direction === 'up') {
                    return {...task, priority: ++task.priority}
                }
                if (direction === 'down') {
                    return {...task, priority: --task.priority}
                }
            } return task;
        });
        setTasks(updatedTasks);
    };


    return (
        <div>

            <div className="container">
                <h1>Kanban</h1>

                {!isOpenCreateTaskForm &&
                <button className="btn btn-primary"
                        onClick={openCreateTaskForm}>Create Task</button>
                }
                {isOpenCreateTaskForm &&
                <form>
                    <div className="form-group">
                        <label htmlFor="exampleInputEmail1">Task</label>
                        <input type="text" className="form-control"
                               value={taskInput}
                               onChange={onTaskChange}/>
                    </div>

                    <div className="input-group mb-3">
                        <div className="input-group-prepend">
                            <label className="input-group-text" htmlFor="inputGroupSelect01">Statuses:</label>
                        </div>
                        <select onChange={(e) => setStatus(e.target.value)} defaultValue='done' className="custom-select" id="inputGroupSelect01">
                            {statuses.sort((a, b) => a - b)
                                .map(el =>
                                <option key={el} value={el}>{el}</option>
                            )}

                        </select>
                    </div>

                    <button type="submit" className="btn btn-primary"
                            onClick={taskSubmit}
                            disabled={!isActiveButtonTaskCreate}>Submit
                    </button>
                    <button className="btn btn-secondary"
                            onClick={taskReset}>Cancel
                    </button>
                </form>
                }
                <hr/>
                <div className="row h-100">

                    <div className="col-sm">
                        TO DO

                        <hr className='mb-10'/>
                        <Column tasks={tasks} priorities={priorities} status='todo' changeStatus={changeStatus} changePriority={changePriority} deleteTask={deleteTask}/>
                    </div>
                    <div className="col-sm">
                        IN PROGRESS
                        <hr className='mb-10'/>
                        <Column tasks={tasks} priorities={priorities} status='progress' changeStatus={changeStatus} changePriority={changePriority} deleteTask={deleteTask}/>
                    </div>
                    <div className="col-sm">
                        REVIEW
                        <hr className='mb-10'/>
                        <Column tasks={tasks} priorities={priorities} status='review' changeStatus={changeStatus} changePriority={changePriority} deleteTask={deleteTask}/>
                    </div>
                    <div className="col-sm">
                        DONE
                        <hr className='mb-10'/>
                        <Column tasks={tasks} priorities={priorities} status='done' changeStatus={changeStatus} changePriority={changePriority} deleteTask={deleteTask}/>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default App;
